.dashboard {
    height: 100vh;
    width: 100vw;
    display: flex;
    /* align-items: center; */
    /* justify-content: center; */
  }
  .dashboard__container {
    display: flex;
    flex-direction: column;
    text-align: center;
    background-color: #dcdcdc;
    padding: 30px;
    max-width: calc(100% - 77.27px);
  }
  .dashboard__btn {
    padding: 10px;
    font-size: 18px;
    margin-top: 10px;
    border: none;
    color: white;
    background-color: black;
  }
  .dashboard-content-container {
    padding-top: 4rem;
    padding-left: 2rem;
  }

